import React from "react";
// @material-ui/core components
import {makeStyles} from "@material-ui/core/styles";

// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";

import classes from "assets/jss/material-kit-react/views/landingPageSections/workStyle.js";

// const classes = makeStyles(styles);

export default class MyForm extends React.Component {
	constructor(props) {
		super(props);
		this.submitForm = this.submitForm.bind(this);
		this.state = {
			status: ""
		};
	}

	render() {
		const {status} = this.state;
		return (
			<div id="workWithUs" style={classes.section}>
				<GridContainer justify="center">
					<GridItem cs={12} sm={12} md={8}>
						<h2 style={classes.title}>Work with us</h2>
						<h4 style={classes.description}>
							Divide details about your product or agency work into parts. Write a
							few lines about each one and contact us about any further
							collaboration. We will responde get back to you in a couple of
							hours.
						</h4>
						<form
							onSubmit={this.submitForm}
							action="https://formspree.io/xdoljady"
							method="POST"
						>
							<GridContainer>
								<GridItem xs={12} sm={12} md={6}>
									<CustomInput
										labelText="Your Name"
										id="name"
										formControlProps={{
											fullWidth: true
										}}
										inputProps={{
											name: "name",
											required: true
										}}
									/>
								</GridItem>
								<GridItem xs={12} sm={12} md={6}>
									<CustomInput
										labelText="Your Email"
										id="email"
										formControlProps={{
											fullWidth: true,
										}}
										inputProps={{
											name: "email",
											required: true
										}}
									/>
								</GridItem>
								<CustomInput
									labelText="Your Message"
									id="message"
									formControlProps={{
										fullWidth: true,
										style: classes.textArea
									}}
									inputProps={{
										multiline: true,
										rows: 5,
										name: "message",
										required: true
									}}
								/>
								<GridContainer justify="center">
									<GridItem xs={12} sm={12} md={4} style={classes.textCenter}>
										{status === "SUCCESS" ? <p>Thanks!</p> :
											<Button type="submit" color="primary">Send Message</Button>}
									</GridItem>
								</GridContainer>
							</GridContainer>
						</form>
						{status === "ERROR" && <p>Ooops! There was an error.</p>}
					</GridItem>
				</GridContainer>
			</div>
		);
	}

	submitForm(ev) {
		ev.preventDefault();
		const form = ev.target;
		const data = new FormData(form);
		console.log(">>>>", data)
		const xhr = new XMLHttpRequest();
		xhr.open(form.method, form.action);
		xhr.setRequestHeader("Accept", "application/json");
		xhr.onreadystatechange = () => {
			if (xhr.readyState !== XMLHttpRequest.DONE) return;
			if (xhr.status === 200) {
				form.reset();
				this.setState({status: "SUCCESS"});
			} else {
				this.setState({status: "ERROR"});
			}
		};
		xhr.send(data);
	}
}
