import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import {makeStyles} from "@material-ui/core/styles";

import styles from "assets/jss/material-kit-react/components/infoStyle.js";

const useStyles = makeStyles(styles);

export default function InfoArea(props) {
	const classes = useStyles();
	const {title, description, iconColor, vertical} = props;
	const iconWrapper = classNames({
		[classes.iconWrapper]: true,
		[classes[iconColor]]: true,
		[classes.iconWrapperVertical]: vertical
	});
	const iconClasses = classNames({
		[classes.icon]: true,
		[classes.iconVertical]: vertical
	});
	console.log(">>>>>>>>>>")
	const titleClass = props.titleClass ? props.titleClass : classes.title;
	const bodyClass = props.bodyClass ? props.bodyClass : classes.infoArea;
	return (
		<div className={bodyClass}>
			{props.icon && <div className={iconWrapper}>
				<props.icon className={iconClasses}/>
			</div>}
			<div className={classes.descriptionWrapper}>
				<h4 className={titleClass}>{title}</h4>
				<p className={classes.description}>{description}</p>
			</div>
		</div>
	);
}

InfoArea.defaultProps = {
	iconColor: "gray"
};

InfoArea.propTypes = {
	icon: PropTypes.object.isRequired,
	title: PropTypes.string.isRequired,
	description: PropTypes.string.isRequired,
	titleClass: PropTypes.object,
	bodyClass: PropTypes.object,
	iconColor: PropTypes.oneOf([
		"primary",
		"warning",
		"danger",
		"success",
		"info",
		"rose",
		"gray"
	]),
	vertical: PropTypes.bool
};
